"use client";
import React, { useCallback, useState } from "react";
import { motion } from "framer-motion";

import { NodeSpace } from "@/components/spatial/NodeSpace";
import { SystemHUD } from "@/components/spatial/SystemHUD";
import { SystemSpeech } from "@/components/spatial/SystemSpeech";
import { ContactOverlay } from "@/components/spatial/ContactOverlay";

export default function Page() {
  const [gravity, setGravity] = useState(0);
  const [isLocked, setIsLocked] = useState(false);

  // ✅ stable callback (prevents effect churn in NodeSpace)
  const handleGravityChange = useCallback((val: number) => {
    setGravity(val);
    if (val >= 100) setIsLocked(true);
  }, []);

  return (
    <main
      className="h-screen w-screen bg-[#050505] overflow-hidden transition-colors duration-1000"
      style={{
        filter: `contrast(${100 + gravity * 0.5}%) brightness(${100 + gravity * 0.2}%)`,
        backgroundColor: gravity > 90 ? "#0a0500" : "#020202",
      }}
    >
      {/* Ambient field response */}
      <motion.div
        animate={{ opacity: gravity / 100 }}
        className="pointer-events-none absolute inset-0
          bg-[radial-gradient(circle_at_center,rgba(255,100,0,0.05)_0%,transparent_70%)]
          mix-blend-screen"
      />

      {/* Core interaction space */}
      <NodeSpace onIntentChange={handleGravityChange} isLocked={isLocked} />

      {/* System readouts */}
      <SystemHUD intent={gravity} />
      <SystemSpeech intent={gravity} />

      {/* Terminal state */}
      {isLocked && <ContactOverlay />}
    </main>
  );
}
