// src/lib/utils.ts
import { type ClassValue, clsx } from "clsx";
import { twMerge } from "tailwind-merge";

/**
 * cn()
 * ------------------------------------------------------------------
 * Safely compose className strings with Tailwind conflict resolution.
 *
 * Usage:
 *  cn("p-4", condition && "bg-red-500", ["text-sm", "font-mono"])
 */
export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs));
}
